import React from 'react'
import Content from '../containers/Content.js'

const Experiences = () => {
        return (
            <Content>
                <div className='experiences'>
                    <h3>Expriences</h3>
                    <h4>Responsable Vendeuse | Maisons du Monde, Chartres Mars 2007 - Fevrier 2018</h4>
                        <p>Jai ete durant 11 ans le bras droit de la Directrice de magasin, ayant a ma tete une 
                        equipe de dix personnes.</p> <p>Jai egalement fait des missions de management sur plusieurs sites.</p> <br/>
                        <p>[management, gestion de boutique, relation clientele, force de vente]</p>
                    
                    <div className='ligne' />  

                    <h4>Fille au pair | Teli Asso, Angleterre 2002 -2003</h4>
                        <p>Bac en poche, jai souhaite ameliorer mon anglais en etant fille au pair a Brighton.</p>
                </div>
            </Content>
        )
    }

export default Experiences



